import React from 'react'
import Mytab from './Mytab'
import { connect } from 'react-redux';

const Cart = (props) => {
    return (
        <div>
            {props.sclist.map((value,index)=>{
                return <div style={{width:'42.5%',marginLeft:'5%',marginTop:'5%',float:'left',backgroundColor:'white',borderRadius:'5px'}} >
                    <img src={value.img} style={{width:'100%',borderRadius:'5px'}}/>
                    <p style={{color:'grey'}}>{value.title}</p>
                    <span style={{color:'red'}}>￥{value.price}</span>
                    <br/>
                    <button onClick={()=>{
                        props.dispatch({
                            type: "DELETE",
                            index: index
                    })}}>取消收藏</button>
                </div> 
            })}
            <Mytab/>
        </div>
    )
}
const mapStateToProps = (state) => ({
    sclist: state.sclist,
})
  
export default connect(mapStateToProps)(Cart);