import store from './taobao/store';
import { Provider } from 'react-redux';
import React from 'react';
import {HashRouter as Router,Route,Switch,Redirect} from 'react-router-dom'
import Home from './taobao/home';
import Cart from './taobao/cart';
import List from './taobao/list';
import My from './taobao/my';
import Goods from './taobao/goods';
import Details from './taobao/details';
import Collect from './taobao/collect';

const App =()=>{
	return <Provider store={store}>
		<Router>
			<Switch>
				<Route exact path='/' component={Home} />
				<Route path='/cart' component={Cart} />
				<Route path='/list' component={List} />
				<Route path='/my' component={My} />
				<Route path='/goods' component={Goods} />
				<Route path='/details' component={Details} />
				<Route path='/collect' component={Collect} />
			</Switch>
		</Router> 
	</Provider>
}

export default App;