import React from 'react';
import {TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
            <TabBar unselectedTintColor="#949494" tintColor="black" barTintColor="white">
                <TabBar.Item
                    title="首页"
                    key="home"
                    icon={ <i className='iconfont icon-home'></i> }
                    selectedIcon={ <i className='iconfont icon-home1'></i> }
                    selected={ pathname === '/' }
                    onPress={ () => {
                        this.props.history.push('/');
                    } }
                    data-seed="logId"
                >
                </TabBar.Item>
                <TabBar.Item
                    icon={
                        <i className='iconfont icon-gouwuche'></i>  
                    }
                    selectedIcon={
                        <i className='iconfont icon-gouwuche1'></i>  
                    }
                    title="收藏"
                    key="cart"
                    selected={ pathname === '/cart' }
                    onPress={ () => {
                        this.props.history.push('/cart')
                    } }
                    data-seed="logId1"
                ></TabBar.Item>
                <TabBar.Item
                    icon={
                        <i className='iconfont icon-dingdan'></i>
                    }
                    selectedIcon={
                        <i className='iconfont icon-icon'></i>
                    }
                    title="订单列表"
                    key="list"
                    selected={ pathname === '/list'  }
                    onPress={ () => {
                        this.props.history.push('/list')
                    } }
                >
			    </TabBar.Item>
                <TabBar.Item
                    icon={<i className='iconfont icon-wode'></i> }
                    selectedIcon={ <i className='iconfont icon-wodedangxuan'></i> }
                    title="我的淘宝"
                    key="my"
                    selected={ pathname === '/my'  }
                    onPress={ () => {
                        this.props.history.push('/my')
                    } }
                >
			</TabBar.Item>	
		  </TabBar>
		</div>
	  );
	}
  }

export default withRouter(Mytab);