const getDate=(list)=>{
    return (dispatch)=>{
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist';
        fetch(url)
        .then(res => res.json())
        .then(res => {
            dispatch({
                type:'GETDATE',
                list:res.data
            })
        })
    }
}

const login=(username)=>{
    return (dispatch)=>{
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login';
        fetch(url, {
            method: 'post',
            headers: {
                'Content-Type': 'application/json'
            },
        })
            .then(res => res.json())
            .then(res => {
                dispatch({
                    type:'LOGIN',
                    username:res.data.name
                })
            })
    }
}
const collect=(sclist)=>{
    return (dispatch)=>{
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist';
        fetch(url)
        .then(res => res.json())
        .then(res => {
            dispatch({
                type:'COLLECT',
                sclist:res.data
            })
        })
    }
}
export {getDate,login,collect}