import React from 'react';
import {NavBar,Carousel} from 'antd-mobile';
import Mytab from './Mytab';


class Collect extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      imgHeight: 176,
      list:[]
  }
}
componentDidMount() {
  setTimeout(() => {
    this.setState({
    });
  }, 100);
}
render(){
  return (<div>
    <div onload={this.getDate()}>
      {this.state.list.map((value)=>{
        return <div style={{width:'42.5%',marginLeft:'5%',marginTop:'5%',float:'left',backgroundColor:'white'}}
          onClick={() => {
            this.props.history.push({
              pathname:'/details',
              src : value.img
            })}} >
          <img src={value.img} style={{width:'100%',borderRadius:'5px 5px 0px 0px'}}/>
          <p style={{color:'grey'}}>{value.title}</p>
          <sapn style={{color:'red'}}>￥{value.price}</sapn>
        </div> 
      })}
    </div>
    <Mytab />
  </div>);
}}


export default Collect