import React from 'react'
import {Icon} from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';

const Details =(props)=> {
      return (<div>
        <Icon 
            type="left" 
            onClick={() => props.history.push('/')}
            style={{borderRadius:'20px',
                backgroundColor:'black',
                opacity:0.5,
                color:'white',
                zIndex:'2',
            }}
        />
        <img
            src= {props.location.src}
            alt=""
            style={{ width: '100%',zIndex:'1',marginTop:'-25px'}}
        />
        <p>￥{props.location.pri}</p>
        <p>{props.location.tit}</p>
        <div style={{position:'absolute',bottom:'0px',backgroundColor:'white',height:'50px'}}>
            <div style={{width:'50px',marginLeft:'20px',float:'left',marginTop:'15px'}}>
                <i className='iconfont icon-shop'></i>
            </div>
            <div className='btn-collect'
                onClick={()=>{
                    let node =document.getElementsByClassName('btn-collect')[0]
                    node.firstChild.className='iconfont icon-favorfill'
                    props.dispatch({
                        type:'COLLECT',
                        sclist:props.location.value
                    })
                }}
                style={{width:'50px',float:'left',marginTop:'15px'}}>
                <i className='iconfont icon-favor'></i>
            </div>
            <div style={{width:'50px',float:'left',marginTop:'15px'}}>
                <i className='iconfont icon-wang'></i>
            </div>
            <div 
                style={{borderRadius:'15px 0px 0px 15px',
                    width:'100px',
                    height:'30px',
                    backgroundColor:'orange',
                    color:'white',
                    fontWeight:'700',
                    float:'left',
                    marginTop:'10px'
            }}>
                <p style={{marginTop:'6px',marginLeft:'20px'}}>加入购物车</p>
            </div>
            <div 
                style={{borderRadius:'0px 15px 15px 0px',
                    width:'100px',
                    height:'30px',
                    backgroundColor:'red',
                    color:'white',
                    fontWeight:'700',
                    float:'left',
                    marginTop:'10px'
            }}>
                <p style={{marginTop:'6px',marginLeft:'20px'}}>立即购买</p>
            </div>
        </div>
      </div>);
    }

const mapStateToProps = (state) => ({
    sclist: state.sclist,
        
})
      
export default connect(mapStateToProps)(withRouter(Details));