import React from 'react';
import {NavBar,Carousel} from 'antd-mobile';
import Mytab from './Mytab';
import { getDate } from './actionCreators';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: ['1', '2', '3','4','5'],
      imgHeight: 176,
  }
}
componentDidMount() {
  setTimeout(() => {
    this.setState({
      data: ['img.alicdn.com/tfs/TB1FjZ7VWL7gK0jSZFBXXXZZpXa-520-280.png', 
      'aecpm.alicdn.com/simba/img/TB1XotJXQfb_uJkSnhJSuvdDVXa.jpg', 
      'aecpm.alicdn.com/simba/img/TB183NQapLM8KJjSZFBSutJHVXa.jpg',
      'aecpm.alicdn.com/simba/img/TB1JNHwKFXXXXafXVXXSutbFXXX.jpg',
      'img.alicdn.com/imgextra/i1/6000000002663/O1CN010OQWKo1VXhLoh2Eze_!!6000000002663-0-octopus.jpg']
    });
  }, 100);
}
render(){
  this.props.dispatch(getDate())
  return (<div>
    <NavBar 
      mode="light"
      icon={<i className='iconfont icon-26' style={{color:'#fff'}}/>}
      style={{backgroundColor:'orange',}}
      rightContent={[
        <div style={{
          width:'300px',
          height:'50%',
          borderRadius:'4px',
          backgroundColor:'red'}} 
          onClick={() => {
            this.props.history.push('/goods')}
        } >
          <i className='iconfont icon-sousuo' 
            style={{color:'#fff',marginLeft:'100px'}}/>
          <p style={{
            marginLeft:'120px',
            marginTop:'-15px',
            color:'white',
          }}>寻找宝贝店铺</p>
        </div>
    ]}>
    </NavBar>
    <Carousel autoplay infinite >
    {this.state.data.map(val => (
      <img
        src={`https://${val}`}
        alt=""
        style={{ width: '100%',left:0}}
        onLoad={() => {
          this.setState({ imgHeight: 'auto' });
        }}/>
    ))}
    </Carousel>
    <div>
      {this.props.list.map((value,index)=>{
        return <div style={{width:'42.5%',marginLeft:'5%',marginTop:'5%',float:'left',backgroundColor:'white'}}
          onClick={() => {
            this.props.history.push({
              pathname:'/details',
              src : value.img,
              tit:value.title,
              pri:value.price,
              value:value
            })}} >
          <img src={value.img} style={{width:'100%',borderRadius:'5px 5px 0px 0px'}}/>
          <p style={{color:'grey'}}>{value.title}</p>
          <sapn style={{color:'red'}}>￥{value.price}</sapn>
        </div> 
      })}
    </div>
    <Mytab />
  </div>);
}}

const mapStateToProps = (state) => ({
    list: state.list,
})

export default connect(mapStateToProps)(withRouter(Home));
